@import "../sass-utils/mixnis";
@import "../sass-utils/vars";


.title-container {
	padding: 60px 0; }


.title-item {
   padding: 35px 0;
   text-align: center;
   border: 2px solid rgba(142, 142, 147, .9);
   border-radius: 8px;
   margin: 20px auto;
   transform: skew(2deg, 2deg);
   background: url("../../../img/bg-ask.jpg") no-repeat 50%/cover;
   box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22), inset 2px 2px 5px rgba(154, 147, 140, 0.5), 1px 1px 5px rgba(255, 255, 255, 1); }

.title-text {
	font-weight: 600;
	letter-spacing: .5px;
	font-size: 45px; }




