.inquirer-container {
  padding-top: 40px; }
  .inquirer-container .title-text {
    font-size: 35px; }
  .inquirer-container p {
    margin-bottom: 20px;
    font-size: 25px;
    display: inline-block;
    background: #feff37; }
  @media (max-width: 550px) {
    .inquirer-container .styles_modal__gNwvD {
      transform: skew(0deg, 0deg); }
    .inquirer-container .ask-one-curve {
      transform: skew(2deg, 2deg); }
      .inquirer-container .ask-one-curve .ask-one {
        transform: skew(-2deg, -2deg); } }
  @media (max-width: 400px) {
    .inquirer-container p {
      font-size: 18px; }
    .inquirer-container .ask-proviso, .inquirer-container label, .inquirer-container .form-control {
      font-size: 16px; }
    .inquirer-container .ul-label {
      margin-left: 0; } }

.ul-label {
  margin-left: 18px; }

.ask-proviso {
  font-size: 20px;
  margin-bottom: 15px; }

[class$=curve],
.styles_modal__gNwvD {
  padding: 40px;
  border: 2px solid rgba(142, 142, 147, 0.9);
  border-radius: 8px;
  margin: 20px auto;
  background: url("../../../img/bg-ask.jpg") no-repeat 50%/cover;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22), inset 2px 2px 5px rgba(154, 147, 140, 0.5), 1px 1px 5px white; }

.ask-one-curve {
  transform: skew(22deg, 2deg); }

.ask-one {
  transform: skew(-22deg, -2deg); }

.ask-two-curve {
  transform: skew(2deg, 2deg); }

label, .form-control {
  font-size: 18px;
  letter-spacing: .5px; }

label {
  background: #feff37; }

input[type="radio"],
input[type="checkbox"] {
  display: none; }
  input[type="radio"]:checked + label:before,
  input[type="checkbox"]:checked + label:before {
    background: linear-gradient(to bottom, #145b32 0%, #fa9baf 100%);
    box-shadow: inset 0px 1px 2px white, 0px 1px 3px rgba(0, 0, 0, 0.5); }

input[type="radio"] + label,
input[type="checkbox"] + label {
  padding: 5px 5px 5px 30px;
  position: relative;
  margin-bottom: 0;
  cursor: pointer; }
  input[type="radio"] + label:before,
  input[type="checkbox"] + label:before {
    content: "";
    position: absolute;
    top: 50%;
    border-radius: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 18px;
    height: 18px;
    border: 2px solid #fff;
    box-shadow: inset 0px 11px 10px white, 0px 1px 3px rgba(0, 0, 0, 0.6);
    background: grey; }

input[type="checkbox"] + label:before {
  border-radius: 0; }

.ask-three-curve,
.ask-four-curve {
  transform: skew(-1deg, 2deg); }

.ask-three,
.ask-four {
  transform: skew(-1deg, 0deg);
  padding: 0px 1px 2px 5px; }

.btn-box-inquirer {
  text-align: center;
  margin: 20px 0 40px; }

.btn {
  padding: 8px 18px 8px 12px;
  margin: 20px 15px;
  letter-spacing: .5px;
  font-size: 25px; }

.styles_modal__gNwvD {
  transform: skew(2deg, 2deg); }
  @media (max-width: 550px) {
    .styles_modal__gNwvD {
      transform: skew(0deg, 0deg); }
      .styles_modal__gNwvD h2 {
        font-size: 24px; } }

.img-modal {
  float: right; }

.btn-box-modal {
  display: flex;
  margin-top: 40px; }
